import React, { useState, useEffect } from 'react'

import Link from 'next/link'

import NavBar from '../components/NavBar'


const BookPage = () => {
    const [test, setTest] = useState([])
    const loremFiller = "God gave you this gift of imagination. Use it. The least little bit can do so much. This painting comes right out of your heart."

    useEffect(() => {
        fetchBooks()
    }, [])

    const fetchBooks = async () => {
        let res = await fetch('https://www.googleapis.com/books/v1/volumes?q=python')
        let data = await res.json()

        setTest(data.items)
        console.log(data.items)
    }

    const renderBooks = () => {
        if(test.length) {
            return test.map(book => {
                return (
                    <div className="row" key={book.id} style={{paddingBottom: "50px"}}>
                        <div className="col-sm-3">
                            <img src={book.volumeInfo.imageLinks.thumbnail}/>
                        </div>
                        <div className="col-sm-9">
                            <h4>{book.volumeInfo.title}</h4>
                            <p className="font-italic">By {book.volumeInfo.authors[0]}</p>
                            <p>{book.volumeInfo.description ? `${book.volumeInfo.description.substring(0, 200)}...` : loremFiller}</p>
                            <span><a href="#">Go to the Amazon page to read more &amp; make me money.</a></span>
                        </div>
                    </div>
                )
            })
        }
        return null
    }

    return (
        <div className="container">
            <h3 className="text-center">Some Python Books!</h3>
            <div class="jumbotron">
                <div class="over container body-content">
                    <h1 class="display-5">A curated directory of books for you(shit kunt) to help you along your python programming peregrination</h1>
                    <p class="lead">Buy them all mate ;)</p>
                </div>
            </div>
            <br />
            <NavBar />
            <br />
            <br />
            <br />
            {renderBooks()}
            <Link href="/index"><button className="justify-content-end btn btn-warning">Back</button></Link>
        </div>
    )
}

export default BookPage